* {
  margin: 0;
  padding: 0;
}

body {
  background-color: whitesmoke;
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 40px;
}

.entry_area {
  margin-top: 30px;
  margin-bottom: 100px;
  color: #494140;
  position: absolute;
  left: 200px;
  background-color: rgb(204, 201, 201);
  margin-top: 50px;
  margin-right: 200px;
  padding: 10px;
}

.main_text {
    
  width: 60%;
  font-size: 20px;
  margin-bottom: 20px;
}

.nav_bar {
  height: 60px;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  background-color: rgb(204, 201, 201);
}

.link {
  text-decoration: none;
  padding-left: 20px;
  padding-right: 20px;
  color: #494140;
}

.text_footnote {
  font-style: italic;
}

.header {
    
  display: flex;
  margin-bottom: 20px;
  font-family:'Courier New';
}